body {
  background-color: #f7f7f7 !important;
}

.heading-block {
  background-color: $danger-color;
  display: inline-block !important;
  border-radius: 10px !important;
  box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.4);
  border: none;
}

.contact-card {
  border-radius: 20px !important;
  background-color: rgba($danger-color, 0.7) !important;
  transition: 1s ease-in-out;
  &:hover {
    background-color: $danger-color !important;
    box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.4);
  }
}

.newsletter-input {
  border-radius: 20px !important;
  background-color: aliceblue !important;
  color: black !important;
}

.hover-card {
  overflow: hidden;
  border-radius: 20px !important;
  img {
    height: 80px;
    width: 80px;
    z-index: 2;
  }
  h5 {
    z-index: 2;
  }

  &:before {
    content: "";
    position: absolute;
    z-index: 1;
    top: -16px;
    right: -16px;
    height: 50px;
    width: 50px;
    border-radius: 50px;
    transform: scale(1);
    transform-origin: 50% 50%;
    background: rgba($warning-color, 0.8);
    transition: transform 0.5s ease-out;
  }

  &:hover:before {
    transform: scale(20);
  }
}
.recent-post-card {
  transition: transform 0.25s ease-out;
  border: 0.5px solid rgba($warning-color, 0.4);
  &:hover {
    transform: scale(1.02);
    border: 0.5px solid rgba($danger-color, 0.4);
  }
}
